<template>
  <div style="height: 100vh; width: 100vw; z-index:100">
    <l-map
      v-if="mapSettings.showMap"
      :zoom="mapSettings.zoom"
      :center="mapSettings.center"
      :options="mapSettings.mapOptions"
      style="height: 100%"
      @update:center="centerUpdate"
      @update:zoom="zoomUpdate"
      ref="myMap"
    >
      <l-tile-layer
        :url="mapSettings.url"
        :attribution="mapSettings.attribution"
      />
      <l-marker
        :visible="markerVisibility.pendingVisible"
        v-for="pendingIssue in pendingIssues"
        :lat-lng="pendingIssue.coordinates"
        :key="pendingIssue.id"
        :icon="pendingIcon"
      >
        <l-popup>
          <v-col class="text-center">
            <h2 class="title" style="line-height:1.2em;padding:0;margin:0">
              {{ pendingIssue.title }}
            </h2>
            <p style="padding:0;margin:0; padding-bottom:10px;">
              {{ pendingIssue.reporter.description }}
            </p>
              <p style="padding:0;margin:0; padding-bottom:10px;">
              id:{{ pendingIssue.id }}
            </p>
            <v-btn
              small
              color="primary"
              @click="changeStatus(pendingIssue.id)"
              >Resolve issue</v-btn
            >
          </v-col>
        </l-popup>
      </l-marker>
      <l-marker
        :icon="resolvedIcon"
        :visible="markerVisibility.resolvedVisible"
        v-for="issue in resolvedIssues"
        :lat-lng="issue.coordinates"
        :key="issue.id"
      >
        <l-popup>
          <div>
            <h2 class="title" style="line-height:1.2em;padding:0;margin:0">
              {{ issue.title }}
            </h2>
            <p style="padding:0;margin:0; padding-bottom:10px;">
              {{ issue.resolver.remark }}
            </p>
            <p style="padding:0;margin:0; padding-bottom:10px;">
              resolved by:{{ issue.resolver.userName }}
            </p>
            <p style="padding:0;margin:0; padding-bottom:10px;">
              id:{{ issue.id }}
            </p>
          </div>
        </l-popup>
      </l-marker>
      <l-marker v-if="locationInfo.gotLocation" :lat-lng="locationInfo.latLong">
        <l-popup class="mx-4" v-if="!markerVisibility.reportDialog">
          <div style="text-align:center">
            <h2 class="title mb-2 pt-2">Your location:</h2>
            <h3 class="subtitle-2">latitude:</h3>
            <p class="text-center overline">
              {{ this.locationInfo.location.coords.latitude }}
            </p>
            <h3 class="subtitle-2">longitude:</h3>
            <p class="text-center overline">
              {{ this.locationInfo.location.coords.longitude }}
            </p>
          </div>
          <div style="text-align:center;" class="my-4 pb-3">
            <v-btn
              small
              class="pink white--text"
              @click="
                markerVisibility.reportDialog = !markerVisibility.reportDialog
              "
              ><v-icon left small>mdi-arrow-right</v-icon
              ><span>report issue</span></v-btn
            >
          </div>
        </l-popup>
      </l-marker>
      <l-polygon
        :visible="boundary.visible"
        v-for="(boundary, index) in boundaries"
        :key="`${index}-${boundary.id}`"
        :lat-lngs="boundary.latlngs"
        :color="boundary.color"
        :weight="boundary.weight"
      ></l-polygon>
    </l-map>
    <v-dialog
      v-model="markerVisibility.resolveDialog"
      max-width="500"
      style="z-index:9000"
    >
      <div v-if="!locationInfo.gotLocation" class="locateRequest">
        <v-card v-if="!locationInfo.gettingLocation" style="padding:1em;">
          <p>Please activate your location to resolve this issue.</p>
          <v-btn @click="locateMe" small class="primary">Get location</v-btn>
        </v-card>
        <v-card :loading="locationInfo.gettingLocation">
          <div v-if="locationInfo.gettingLocation" style="padding:1em;">
            <i>Getting your location...</i>
          </div>
        </v-card>
        <v-card v-if="locationInfo.errorStr">
          <div v-if="locationInfo.errorStr">
            Sorry, but the following error occurred:
            {{ locationInfo.errorStr }}
          </div>
        </v-card>
      </div>
      <v-card v-if="locationInfo.gotLocation" style="padding:1em;">
        <v-form ref="form" lazy-validation>
          <v-text-field
            :value="this.locationInfo.location.coords.longitude"
            label="Longitude"
            required
          ></v-text-field>
          <v-text-field
            :value="this.locationInfo.location.coords.latitude"
            label="Latitude"
            required
          ></v-text-field>
          <v-btn class="primary">resolve</v-btn>
        </v-form>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="markerVisibility.reportDialog"
      max-width="600px"
      style="z-index:9000"
    >
      <v-card v-if="locationInfo.gotLocation" style="padding:1em;">
        <v-card-title>
          <h2 class="title">Add a new report at your location:</h2>
        </v-card-title>
        <v-card-text>
          <v-form class="px-3 text-left" ref="reportForm">
            <div hidden>
              <v-text-field
                :value="this.locationInfo.location.coords.longitude"
                label="Longitude"
                required
              ></v-text-field>
              <v-text-field
                :value="this.locationInfo.location.coords.latitude"
                label="Latitude"
                required
              ></v-text-field>
            </div>
            <v-text-field
              class="mt-4"
              :rules="rules.titleRules"
              v-model="newIssue.title"
              label="Title"
              required
            ></v-text-field>
            <v-text-field
              prepend-icon="mdi-account"
              class="mt-4"
              :rules="rules.nameRules"
              v-model="newIssue.reporter.userName"
              label="User name"
              required
            ></v-text-field>
            <v-text-field
              prepend-icon="mdi-email"
              class="mt-4"
              :rules="rules.emailRules"
              v-model="newIssue.reporter.email"
              label="E-mail"
              required
            ></v-text-field>
            <v-textarea
              label="Description"
              v-model="newIssue.reporter.description"
              :rules="rules.descriptionRules"
              prepend-icon="mdi-pencil"
              class="my-4 pt-3"
              required
            ></v-textarea>
            <v-btn class="pink white--text" @click="submit" :loading="loading"
              >Add issue</v-btn
            >
          </v-form>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-snackbar
      vertical
      left
      v-model="reportSnackbar.snackbar"
      :timeout="reportSnackbar.timeout"
    >
      {{ reportSnackbar.text }}
      <v-btn color="blue" text @click="reportSnackbar.snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import db from "@/fb";

import { bus } from "./main";
import { LMap, LTileLayer, LMarker, LPopup, LPolygon } from "vue2-leaflet";

import L from "leaflet";
delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
  iconSize: [38, 59],
  iconAnchor: [20, 59],
  popupAnchor: [-3, -76],
  shadowSize: [41, 41],
  shadowAnchor: [14, 41]
});

export default {
  props: [
    "boundaries",
    "issues",
    "markerVisibility",
    "mapSettings",
    "locationInfo"
  ],

  name: "myMap",
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
    LPolygon
  },

  data() {
    return {
      reportSnackbar: {
        snackbar: false,
        text: "Your report is added",
        timeout: 20000
      },
      loading: false,
      rules: {
        titleRules: [
          v => !!v || "Title is required",
          v => v.length <= 30 || "Title must be less than 30 characters"
        ],
        nameRules: [
          v => !!v || "Name is required",
          v => v.length <= 20 || "Name must be less than 20 characters"
        ],
        descriptionRules: [
          v => !!v || "Description is required",
          v => v.length <= 100 || "Description must be less than 100 characters"
        ],
        emailRules: [
          v => !!v || "E-mail is required",
          v => /.+@.+/.test(v) || "E-mail must be valid"
        ]
      },

      newIssue: {
        title: "Dogshit on the street",
        reporter: {
          email: "mr@smith.de",
          userName: "Mr Smith",
          description: "This is the description of the issue"
        },
        resolver: {
          email: "john@doe.com",
          userName: "John Doe",
          remark: "Feedback of the resolver"
        },
        coordinates: this.locationInfo.latLong,
        status: "pending",
        permanent: false
      },

      pendingIcon: L.icon({
        iconUrl: require("@/assets/pending.png"),
        iconSize: [38, 59],
        iconAnchor: [20, 59],
        popupAnchor: [-3, -76],
        shadowUrl: require("@/assets/my-icon-shadow.png"),
        shadowSize: [41, 41],
        shadowAnchor: [14, 41]
      }),
      resolvedIcon: L.icon({
        iconUrl: require("@/assets/resolved.png"),
        iconSize: [38, 59],
        iconAnchor: [20, 59],
        popupAnchor: [-3, -76],
        shadowUrl: require("@/assets/my-icon-shadow.png"),
        shadowSize: [41, 41],
        shadowAnchor: [14, 41]
      })
    };
  },
  methods: {
    changeStatus(id) {
      const status = 'resolved';
  db.collection('issues')
    .where('id', '==', id)
    .get()
    .then(snap => {
      return snap.docs[0].ref.update({
          status: status
        });
    })
    .then(() => {
      alert('Successfully updated the record')
    })
    .catch(error => {
      alert('There was an error editing the record: ' + error)
    })
},
    submit() {
      if (this.$refs.reportForm.validate()) {
        this.loading = true;
        const newIssueObject = {
          title: this.newIssue.title,
          reporter: {
            email: this.newIssue.reporter.email,
            userName: this.newIssue.reporter.userName,
            description: this.newIssue.reporter.description
          },
          resolver: {
            email: this.newIssue.resolver.email,
            userName: this.newIssue.resolver.userName,
            remark: this.newIssue.resolver.remark
          },
          coordinates: this.locationInfo.latLong,
          status: this.newIssue.status,
          permanent: this.newIssue.permanent
        };
        db.collection("issues")
          .add(newIssueObject)
          .then(() => {
            this.loading = false;
            this.markerVisibility.reportDialog = false;
            this.reportSnackbar.snackbar = true;
          });
      }
    },
    zoomUpdate(zoom) {
      this.mapSettings.currentZoom = zoom;
    },
    centerUpdate(center) {
      this.mapSettings.currentCenter = center;
    },
    showLongText() {
      this.mapSettings.showParagraph = !this.mapSettings.showParagraph;
    },
    innerClick() {
      alert("Click!");
    },
    async getLocation() {
      return new Promise((resolve, reject) => {
        if (!("geolocation" in navigator)) {
          reject(new Error("Geolocation is not available."));
        }

        navigator.geolocation.getCurrentPosition(
          pos => {
            resolve(pos);
          },
          err => {
            reject(err);
          }
        );
      });
    },
    async locateMe() {
      try {
        this.locationInfo.gettingLocation = true;
        this.locationInfo.location = await this.getLocation();
        this.locationInfo.gettingLocation = false;
        this.locationInfo.gotLocation = true;
        this.$refs.myMap.mapObject.setView(
          [
            this.locationInfo.location.coords.latitude,
            this.locationInfo.location.coords.longitude
          ],
          this.$refs.myMap.mapObject.getZoom(),
          {
            animate: true,
            duration: 2
          }
        ),
          (this.locationInfo.latLong = [
            this.locationInfo.location.coords.latitude,
            this.locationInfo.location.coords.longitude
          ]);
      } catch (e) {
        this.locationInfo.gettingLocation = false;
        this.locationInfo.errorStr = e.message;
      }
    }
  },
  computed: {
    pendingIssues: function() {
      return this.issues.filter(issue => {
        return issue.status.match("pending");
      });
    },
    resolvedIssues: function() {
      return this.issues.filter(issue => {
        return issue.status.match("resolved");
      });
    }
  },
  created() {
    db.collection("issues").onSnapshot(res => {
      const changes = res.docChanges();
      changes.forEach(change => {
        if (change.type === "added") {
          this.issues.push({
            ...change.doc.data(),
            id: change.doc.id
          });
        }
      });
    });
  },
  mounted() {
    bus.$on("lockTarget", () => {
      if (this.locationInfo.gotLocation) {
        this.$refs.myMap.mapObject.setView(
          [
            this.locationInfo.location.coords.latitude,
            this.locationInfo.location.coords.longitude
          ],
          this.$refs.myMap.mapObject.getZoom(),
          {
            animate: true,
            duration: 2
          }
        );
      } else this.locateMe();
    });
  }
};
</script>
<style scoped>
.v-progress-linear {
  height: 5px !important;
}
.v-text-field {
  padding-top: 0 !important;
  margin-top: 0 !important;
}
p.text-center {
  padding: 0;
  margin: 0;
}
/*.leaflet-popup-content p {padding:0 !important; margin:0}*/
</style>
