<template>
  <div class="overflow-hidden">
    <v-speed-dial
      v-model="menu"
      absolute
      dark
      fab
      top
      right
      direction="bottom"
      transition="slide-y-transition"
      style="z-index:2000"
    >
      <template v-slot:activator>
        <v-btn v-model="menu" dark fab @click.Stop="fab=false">
          <v-icon v-if="menu">mdi-close</v-icon>
          <v-icon v-else>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-btn
        fab
        dark
        small
        @click="
          markerVisibility.introDialog = !markerVisibility.introDialog
        "
      >
        <v-icon>mdi-help</v-icon>
      </v-btn>
      <v-btn fab dark small @click.stop="drawer = !drawer">
        <v-icon>mdi-information-outline</v-icon>
      </v-btn>
      <v-btn fab dark small @click="targetLocation">
        <v-icon>mdi-crosshairs</v-icon>
      </v-btn>
    </v-speed-dial>

    <v-speed-dial
      v-model="fab"
      absolute
      dark
      fab
      bottom
      right
      direction="top"
      transition="slide-y-reverse-transition"
      style="z-index:2000"
    >
      <template v-slot:activator>
        <v-btn v-model="fab" dark fab @click="menu=false">
          <v-icon v-if="fab">mdi-close</v-icon>
          <v-icon v-else>mdi-layers-triple</v-icon>
        </v-btn>
      </template>
      <v-btn
      color="blue"
        fab
        dark
        small
        v-for="(boundary, index) in boundaries"
        :key="`${index}-${boundary.id}`"
        @click.stop="boundary.visible = !boundary.visible"
      >
        <v-icon>mdi-circle-double</v-icon>
      </v-btn>
      <v-btn
            color="pink"
        fab
        dark
        small
        @click.stop="
          markerVisibility.pendingVisible = !markerVisibility.pendingVisible
        "
      >
        <v-icon>mdi-exclamation-thick</v-icon>
      </v-btn>
      <v-btn
            color="light-green"

        fab
        dark
        small
        @click.stop="
          markerVisibility.resolvedVisible = !markerVisibility.resolvedVisible
        "
      >
        <v-icon>mdi-check-bold</v-icon>
      </v-btn>
    </v-speed-dial>

    <v-navigation-drawer
      v-model="drawer"
      absolute
      left
      temporary
      style="z-index:2000"
      dark
      >
      <v-container style="padding-top:30px">
      <v-card-text class="white--text title">Map Legend</v-card-text>
      <v-card-text class="white--text caption"><v-icon dark left small>mdi-crosshairs</v-icon>Center viewport</v-card-text>
            <v-card-text class="white--text caption"><v-icon dark left small>mdi-layers-triple</v-icon>Layers</v-card-text>
      <v-card-text class="white--text caption"><v-icon dark left small>mdi-circle-double</v-icon>Boundaries</v-card-text>
      <v-card-text class="white--text caption"><v-icon dark left small>mdi-exclamation-thick</v-icon>Pending reports</v-card-text>
      <v-card-text class="white--text caption"><v-icon dark left small>mdi-check-bold</v-icon>Resolved reports</v-card-text>
</v-container>
      <v-container style="padding-top:30px">
      <v-card-text class="white--text font-weight-light caption">Click your location icon to report something. You can only report at your location.</v-card-text>
      </v-container>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { bus } from "../main";

export default {
  props: ["boundaries", "issues", "markerVisibility"],
  name: "Menu",

  data: () => ({
    menu: false,
    drawer: false,
    dialog: false,
    group: null,
    direction: "top",
    fab: false,
    fling: false,
    hover: false,
    tabs: null,
    top: false,
    right: true,
    bottom: true,
    left: false,
    checkbox1: true,
    checkbox2: false,
    transition: "slide-y-reverse-transition",
    menuItems: [
      { id: 0, item: "home" },
      { id: 1, item: "about" },
      { id: 2, item: "contact" }
    ],
    valid: false,
    firstname: "",
    lastname: "",
    nameRules: [
      v => !!v || "Name is required",
      v => v.length <= 10 || "Name must be less than 10 characters"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+/.test(v) || "E-mail must be valid"
    ]
  }),
  watch: {
    group() {
      this.drawer = false;
    }
  },
  computed: {
    activeFab() {
      switch (this.tabs) {
        case "one":
          return { class: "purple", icon: "account_circle" };
        case "two":
          return { class: "red", icon: "edit" };
        case "three":
          return { class: "green", icon: "keyboard_arrow_up" };
        default:
          return {};
      }
    }
  },
  methods: {
    targetLocation(){
      bus.$emit("lockTarget");
    }
  }
};
</script>
<style scoped>
.v-speed-dial--bottom {
  bottom: 35px;
}
.v-speed-dial--direction-top .v-speed-dial__list {
  margin-right: 20px !important;
}
#create .v-speed-dial {
  position: absolute;
}

#create .v-btn--floating {
  position: relative;
}
button {
  color: white;
}
a {
  color: white !important;
}
.v-btn--fab.v-size--default.v-btn--absolute.v-btn--top {
  top: 20px;
}
.v-form .container .row > div {
  padding: 0px 10px;
}
.v-card__text{padding:3px;}
</style>
